<template>
  <el-scrollbar>
    <el-input
      v-model="serchKey"
      class="w-50 m-2"
      size="large"
      placeholder="input something (The Length >= 2)"
      :prefix-icon="Search"
    />
    <el-menu
      ref="menuRef"
      @open="handleOpen"
      @close="handleClose"
      style="overflow-x: hidden"
    >
      <el-sub-menu
        :index="index1 + ''"
        v-for="(menu, index1) in menus"
        :key="index1"
        :is-active="true"
        :title="menu.url"
      >
        <template #title>
          <el-icon v-if="menu.success"><Flag /></el-icon>
          <el-icon v-if="!menu.success" title="update fail">
            <CircleCloseFilled />
          </el-icon>
          <span>{{ menu.name }}</span>
        </template>
        <el-sub-menu
          :index="index1 + '-' + index2"
          v-for="(smenu, index2) in menu.children"
          :key="index2"
          v-show="smenu.show"
        >
          <template #title>
            <el-icon><Menu /></el-icon>
            <span>{{ smenu.name }}</span>
          </template>
          <el-menu-item
            :index="index1 + '-' + index2 + '-' + index3"
            v-for="(tmenu, index3) in smenu.children"
            :key="index3"
            :title="tmenu.path"
            v-show="tmenu.show"
            @click="router(tmenu.name, tmenu.apiUrl, tmenu.path)"
          >
            {{ tmenu.name }}
          </el-menu-item>
        </el-sub-menu>
      </el-sub-menu>
    </el-menu>
  </el-scrollbar>
</template>

<script setup>
import { inject, ref, watch } from "vue";
import { Search } from "@element-plus/icons-vue";
import axios from "axios";

const serchKey = ref("");
const menus = ref([]);
const menuRef = ref();
const goRouter = inject("goRouter");
const queryMenu = queryMenuCenter();
watch(serchKey, async (newQuestion, oldQuestion) => {
  if (oldQuestion.length >= 2 && newQuestion.length < 2) {
    openOrCloseMenu(false);
    return;
  } else if (newQuestion.length < 2) {
    return;
  }
  queryMenu(newQuestion);
});

function queryMenuCenter() {
  let timers = null;
  return function query(newQuestion) {
    newQuestion = newQuestion.toLowerCase();
    if (timers != null) {
      clearTimeout(timers);
    }
    timers = setTimeout(() => {
      openOrCloseMenu(true);
      //检索
      for (let index in menus.value) {
        for (let sindex in menus.value[index].children) {
          menus.value[index].children[sindex].show = false;
          for (let tindex in menus.value[index].children[sindex].children) {
            let menu = menus.value[index].children[sindex].children[tindex];
            menu.show = false;
            if (menu.name == undefined) menu.name = "";
            if (menu.path == undefined) menu.path = "";
            if (
              menu.name.toLowerCase().indexOf(newQuestion) >= 0 ||
              menu.path.toLowerCase().indexOf(newQuestion) >= 0
            ) {
              menus.value[index].children[sindex].show = true;
              menu.show = true;
            }
          }
        }
      }
    }, 1000);
  };
}

function router(title, apiUrl, path) {
  goRouter(title, apiUrl, path);
}

function openOrCloseMenu(isOpen) {
  let menuIndex = [];
  for (let i = 0; i < menus.value.length; i++) {
    menuIndex.push(i + "");
    let second = menus.value[i].children;
    for (let j = 0; j < second.length; j++) {
      menuIndex.push(i + "-" + j);
    }
  }
  for (let i in menuIndex) {
    if (isOpen) {
      menuRef.value.open(menuIndex[i]);
    } else {
      menuRef.value.close(menuIndex[i]);
    }
  }
}

const getMenu = function () {
  axios
    .get("/service/getMenu")
    .then((response) => {
      menus.value = response.data;
      if (serchKey.value.length >= 2) {
        queryMenu(serchKey.value);
      }
    })
    .catch(function (error) {
      // 请求失败处理
      console.log("error:");
      console.log(error);
    });
};

function getErrorInfo() {
  axios
    .get("/service/getError", {})
    .then((response) => {
      console.table(response);
    })
    .catch(function (error) {
      // 请求失败处理
      console.log("error:");
      console.log(error);
    });
}

getMenu();
getErrorInfo();
setInterval(() => {
  getMenu();
  getErrorInfo();
}, 5 * 60 * 1000);

const handleOpen = function (key) {
  if (serchKey.value.length >= 2) {
    return;
  }
  menuToggle(key, true);
};
const handleClose = function (key) {
  if (serchKey.value.length >= 2) {
    return;
  }
  menuToggle(key, false);
};
const menuToggle = function (key, isOpen) {
  let menuIndex = (key + "").split("-");
  let loadMenu = menus.value;
  for (let item in menuIndex) {
    let ikey = menuIndex[item];
    if (item == menuIndex.length - 1) {
      for (let index in loadMenu[ikey].children) {
        let menu = loadMenu[ikey].children[index];
        menu.show = isOpen;
      }
    }

    loadMenu = loadMenu[ikey].children;
  }
  console.log(key);
};
</script>

<style>
.el-menu-vertical-demo:not(.el-menu--collapse) {
  width: 200px;
  min-height: 400px;
}
</style>
