<template>
  <el-row class="full-height" @mouseup="mouseup">
    <el-col
      :span="2"
      style="display: flex; min-height: 100vh; flex-direction: column"
    >
      <el-affix
        position="bottom"
        :offset="100"
        style="margin-top: auto; cursor: pointer"
      >
        <a href="#topMiao">
          <el-button type="primary" circle plain>
            <el-icon title="To Top"><CaretTop /></el-icon>
          </el-button>
        </a>
      </el-affix>
    </el-col>
    <el-col :span="20" :offset="0">
      <div class="common-layout full-height">
        <el-container class="full-height">
          <el-header id="topMiao" style="display: flex; color: #337ecc">
            <div style="display: flex; margin: auto">
              <div style="display: flex; align-items: center">
                <el-avatar :size="23" src="/favicon.png" />
              </div>
              <div>
                <HelloWorld msg="Dogdoc" />
              </div>
            </div>
            <div
              style="
                display: flex;
                width: 150px;
                align-items: center;
                justify-content: end;
              "
            >
              <el-popover placement="bottom" trigger="click">
                <template #reference>
                  <el-avatar :size="30">
                    <el-icon style="font-size: 20px; cursor: pointer">
                      <Setting />
                    </el-icon>
                  </el-avatar>
                </template>
                <template #default>
                  <el-select v-model="$i18n.locale">
                    <el-option label="中文" value="zh" />
                    <el-option label="English" value="en" />
                  </el-select>
                </template>
              </el-popover>
            </div>
          </el-header>
          <el-container @mousemove="mousemove">
            <el-aside class="full-height elevation-1" :width="asideWidth">
              <Menu />
            </el-aside>
            <div class="full-height movebtn" @mousedown="mousedown"></div>
            <el-main class="full-height" style="padding: 0px 20px">
              <el-scrollbar>
                <el-tabs
                  ref="tabRef"
                  v-model="editableTabsValue"
                  type="border-card"
                  class="tabs"
                  closable
                  @tab-remove="removeTab"
                  @tab-click="clickTab"
                >
                  <el-tab-pane
                    v-for="item in editableTabs"
                    :key="item.name"
                    :name="item.name"
                    :pathApiUrl="item.apiUrl"
                    :pathUrl="item.path"
                  >
                    <template #label>
                      <el-tooltip
                        :content="item.apiHost"
                        placement="bottom"
                        effect="light"
                      >
                        <span>
                          {{ item.title }}
                        </span>
                      </el-tooltip>
                    </template>
                    <Main ref="mainRef" :pathUrl="item.path"></Main>
                  </el-tab-pane>
                </el-tabs>
              </el-scrollbar>
            </el-main>
          </el-container>
          <el-footer></el-footer>
        </el-container>
      </div>
    </el-col>
  </el-row>
</template>

<script setup>
import { ref, defineComponent, provide, onMounted } from "vue";
import { myToolsRef } from "./common";
import HelloWorld from "./components/HelloWorld.vue";
import Menu from "./components/Menu.vue";
import Main from "./components/Main.vue";
import axios from "axios";

let moveFlag = false;
let originPosition = 0;
let originWith = 300;
let originAsideWidth = originWith;
const asideWidth = ref(originAsideWidth + "px");
const mainRef = ref(null);
const tabRef = ref(null);

defineComponent({
  name: "App",
  components: {
    HelloWorld,
    Menu,
    Main,
  },
});
onMounted(function () {
  let tabs = JSON.parse(localStorage.getItem("tabs"));
  if (tabs != undefined && tabs.length > 0) {
    editableTabs.value = tabs;
    let activeTab = tabs[tabs.length - 1];
    editableTabsValue.value = activeTab.name;
    tabIndex = activeTab.name;
    getApiInfo(activeTab.apiUrl, activeTab.path);
  }
});
function truncateLabel(label) {
  if (label == undefined) {
    return "";
  }
  const maxLength = 10; // 设置最大长度
  if (label.length > maxLength) {
    return label.slice(0, maxLength) + "..."; // 超过最大长度时截断并添加省略号
  }
  return label;
}

function mouseup() {
  moveFlag = false;
}
function mousedown(e) {
  moveFlag = true;
  originPosition = e.clientX;
  originWith = originAsideWidth;
}
function mousemove(e) {
  if (moveFlag) {
    //$(".elevation-1").offsetLeft
    if (e.clientX - originPosition + originWith < 10) {
      moveFlag = false;
      originPosition = 0;
      originAsideWidth = originWith;
      asideWidth.value = originWith + "px";
      return;
    }
    originAsideWidth = e.clientX - originPosition + originWith;
    asideWidth.value = e.clientX - originPosition + originWith + "px";
  }
}

function goRouter(title, apiUrl, path) {
  let exists = false;
  let tabModel = {};
  for (let i = 0; i < editableTabs.value.length; i++) {
    if (editableTabs.value[i].path == path) {
      tabModel = editableTabs.value[i];
      exists = true;
      break;
    }
  }
  if (exists) {
    editableTabsValue.value = tabModel.name;
  } else {
    addTab(title, path, apiUrl);
  }
  getApiInfo(apiUrl, path);
}

function getApiInfo(apiUrl, path) {
  axios
    .get("/service/getApiInfo", {
      params: {
        apiUrl,
        path,
      },
    })
    .then((response) => {
      for (let index in mainRef.value) {
        if (mainRef.value[index].$el.attributes["pathUrl"].value == path) {
          response.data.apiUrl = apiUrl;
          mainRef.value[index].loadMain(response.data);
          break;
        }
      }
    })
    .catch(function (error) {
      // 请求失败处理
      console.log(error);
    });
}

let tabIndex = 0;
const editableTabsValue = ref("");
const editableTabs = ref([]);

const addTab = (targetName, path, apiUrl) => {
  const newTabName = `${++tabIndex}`;
  editableTabs.value.push({
    title: truncateLabel(targetName),
    name: newTabName,
    path: path,
    apiUrl: apiUrl,
    apiHost: myToolsRef.value.getHost(apiUrl),
  });
  localStorage.setItem("tabs", JSON.stringify(editableTabs.value));
  editableTabsValue.value = newTabName;
};
const removeTab = (targetName) => {
  const tabs = editableTabs.value;
  let activeName = editableTabsValue.value;
  if (activeName === targetName) {
    tabs.forEach((tab, index) => {
      if (tab.name === targetName) {
        const nextTab = tabs[index + 1] || tabs[index - 1];
        if (nextTab) {
          activeName = nextTab.name;
        }
      }
    });
  }

  editableTabsValue.value = activeName;
  editableTabs.value = tabs.filter((tab) => tab.name !== targetName);
  localStorage.setItem("tabs", JSON.stringify(editableTabs.value));
};
const clickTab = (pane, ev) => {
  let tab = ev.target;
  if (tab.nodeName.toLocaleLowerCase() == "span") {
    tab = tab.parentElement;
  }

  let tabModel = tabRef.value.$el.querySelector(
    "#" + tab.attributes["aria-controls"].value
  );
  getApiInfo(
    tabModel.attributes["pathApiUrl"].value,
    tabModel.attributes["pathUrl"].value
  );
};

provide("timeformat", myToolsRef.value.timeformat);
provide("goRouter", goRouter);
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  height: calc(100vh - 16px);
}
.full-height {
  height: 100%;
}
.elevation-1 {
  box-shadow: 4px 0px 4px -2px rgba(0, 0, 0, 0.2);
}
.movebtn {
  display: flex;
  width: 3px;
}
.movebtn:hover {
  cursor: ew-resize;
  background-color: papayawhip;
}
</style>
