<template>
  <div class="demo-collapse">
    <el-collapse v-model="activeNames" @change="handleChange">
      <!-------------------------------------  基本信息  --------------------------------------------->
      <el-collapse-item name="1">
        <template #title>
          <el-icon><DocumentRemove /></el-icon>
          {{ $t("main.generalInfo") }}
        </template>
        <div>
          <el-descriptions :column="1">
            <el-descriptions-item :label="$t('main.apiName') + ':'">
              {{ info.name }}
            </el-descriptions-item>
            <el-descriptions-item :label="$t('main.updateTime') + ':'">
              {{ info.time }}
            </el-descriptions-item>
            <el-descriptions-item :label="$t('main.reqType') + ':'">
              <el-tag class="ml-2" type="success">{{ info.type }}</el-tag>
            </el-descriptions-item>
            <el-descriptions-item :label="$t('main.reqPath') + ':'">
              <el-tag>{{ info.url }}</el-tag>
            </el-descriptions-item>
            <el-descriptions-item :label="$t('main.sendRequest') + ':'">
              <el-button
                type="warning"
                round
                plain
                size="small"
                @click="dialogVisible = true"
                style="cursor: pointer"
              >
                {{ $t("main.startSend") }}
              </el-button>
            </el-descriptions-item>
          </el-descriptions>
        </div>
      </el-collapse-item>
      <!-------------------------------------  请求Header  --------------------------------------------->
      <el-collapse-item
        name="2"
        v-if="info.header != undefined && info.header.length > 0"
      >
        <template #title>
          <el-icon><Memo /></el-icon>
          {{ $t("main.reqHeader") }}
        </template>
        <el-descriptions :column="1" border>
          <div v-for="item in info.header" :key="item.name">
            <el-descriptions-item :label="item.name">
              {{ item.description }}
            </el-descriptions-item>
          </div>
        </el-descriptions>
      </el-collapse-item>
      <!-------------------------------------  请求params参数  --------------------------------------------->
      <el-collapse-item
        name="3"
        v-if="info.params != undefined && info.params.length > 0"
      >
        <template #title>
          <el-icon><Postcard /></el-icon>
          {{ $t("main.reqParameters") }}
        </template>
        <el-table :data="info.params" row-key="name" stripe style="width: 100%">
          <el-table-column
            prop="name"
            :label="$t('main.model.name')"
            width="250"
          />
          <el-table-column
            prop="type"
            :label="$t('main.model.type')"
            width="250"
          />
          <el-table-column :label="$t('main.model.nullabled')" width="80">
            <template #default="scope">
              <el-tag
                class="ml-2"
                :type="scope.row.nullable == true ? 'warning' : 'danger'"
              >
                {{ scope.row.nullable == true ? "Yes" : "No" }}
              </el-tag>
            </template>
          </el-table-column>
          <el-table-column prop="description" :label="$t('main.model.desc')" />
        </el-table>
      </el-collapse-item>
      <!-------------------------------------  请求body参数  --------------------------------------------->
      <el-collapse-item
        name="4"
        v-if="info.body != undefined && info.body.length > 0"
      >
        <template #title>
          <el-icon><Postcard /></el-icon>
          {{ $t("main.reqBody") }}
        </template>
        <el-table
          :data="info.body"
          row-key="name"
          stripe
          style="width: 100%"
          :expand-row-keys="[info.body[0].name]"
        >
          <el-table-column
            prop="name"
            :label="$t('main.model.name')"
            width="250"
          />
          <el-table-column
            prop="type"
            :label="$t('main.model.type')"
            width="250"
          />
          <el-table-column :label="$t('main.model.nullabled')" width="80">
            <template #default="scope">
              <el-tag
                class="ml-2"
                :type="scope.row.nullable == true ? 'warning' : 'danger'"
                v-if="
                  scope.row.children == undefined ||
                  scope.row.children.length == 0
                "
              >
                {{ scope.row.nullable == true ? "Yes" : "No" }}
              </el-tag>
            </template>
          </el-table-column>
          <el-table-column prop="description" :label="$t('main.model.desc')" />
        </el-table>
      </el-collapse-item>
      <!-------------------------------------  返回参数  --------------------------------------------->
      <el-collapse-item
        name="5"
        v-if="info.result != undefined && info.result.length > 0"
      >
        <template #title>
          <el-icon><Tickets /></el-icon>
          {{ $t("main.resData") }}
        </template>
        <el-table
          :data="info.result"
          row-key="name"
          stripe
          style="width: 100%"
          :expand-row-keys="[info.result[0].name]"
        >
          <el-table-column
            prop="name"
            :label="$t('main.model.desc')"
            width="250"
          />
          <el-table-column
            prop="type"
            :label="$t('main.model.type')"
            width="250"
          />
          <el-table-column prop="description" :label="$t('main.model.desc')" />
        </el-table>
      </el-collapse-item>
    </el-collapse>
    <el-dialog
      v-model="dialogVisible"
      :title="$t('main.sendRequest')"
      width="45%"
      draggable
      :modal="true"
      @close="closeDialog"
    >
      <Postman ref="postmanRef" :Data="info"></Postman>
    </el-dialog>
  </div>
</template>
<script setup>
import { ref, inject, defineExpose } from "vue";
import Postman from "./Postman.vue";

const activeNames = ref(["1", "2", "3", "4", "5"]);
const timeformat = inject("timeformat");
const info = ref({});

const dialogVisible = ref(false);
const postmanRef = ref();

function handleChange() {}

function closeDialog() {
  //postmanRef.value.status = "";
}

function loadMain(data) {
  info.value = {
    name: data.name,
    time: timeformat(data.time, "yyyy-MM-dd HH:mm:ss"),
    url: data.url,
    type: data.type,
    header: data.header,
    params: data.params,
    body: data.bodyParam,
    result: data.requestResult,
    apiUrl: data.apiUrl,
  };
}

defineExpose({ loadMain });
</script>

<style scoped>
.demo-collapse /deep/ .el-descriptions__label {
  font-weight: bolder;
}
.demo-collapse /deep/ .el-collapse-item__header {
  font-size: 16px;
}
.demo-collapse /deep/ .el-icon {
  font-size: 14px;
}
.demo-collapse /deep/ .el-dialog__body {
  padding: 10px 20px !important;
}
</style>
