<template>
  <el-scrollbar height="calc(100vh * 0.5)" style="padding: 0 8px">
    <el-collapse v-model="activeNames" @change="handleChange">
      <!-------------------------------------  基本信息  --------------------------------------------->
      <el-collapse-item name="1">
        <template #title>
          <el-icon><DocumentRemove /></el-icon>
          {{ $t("postman.request") }}
        </template>
        <div>
          <el-tabs v-model="activeName" type="border-card" class="tabs">
            <el-tab-pane label="Header" name="first">
              <el-table
                ref="headerRef"
                :data="Header"
                style="width: 100%"
                @selection-change="handleSelectionChangeHeader"
              >
                <el-table-column type="selection" width="30" />
                <el-table-column label="Key" width="200" show-overflow-tooltip>
                  <template #default="scope">
                    <el-input
                      v-model="scope.row.name"
                      type="input"
                      placeholder="Please input"
                    />
                  </template>
                </el-table-column>
                <el-table-column label="Value" width="200">
                  <template #default="scope">
                    <el-input
                      v-model="scope.row.value"
                      type="input"
                      placeholder="Please input"
                    />
                  </template>
                </el-table-column>
                <el-table-column
                  property="description"
                  label="Description"
                  show-overflow-tooltip
                />
              </el-table>
            </el-tab-pane>
            <el-tab-pane
              label="Params"
              name="second"
              :disabled="Params.length < 1"
            >
              <el-table
                ref="paramsRef"
                :data="Params"
                style="width: 100%"
                @selection-change="handleSelectionChangeParams"
              >
                <el-table-column type="selection" width="30" />
                <el-table-column label="Key" width="200" show-overflow-tooltip>
                  <template #default="scope">
                    <el-input
                      v-model="scope.row.name"
                      type="input"
                      placeholder="Please input"
                    />
                  </template>
                </el-table-column>
                <el-table-column label="Value" width="200">
                  <template #default="scope">
                    <el-input
                      v-model="scope.row.value"
                      type="input"
                      placeholder="Please input"
                    />
                  </template>
                </el-table-column>
                <el-table-column label="Description" show-overflow-tooltip>
                  <template #default="scope">
                    {{ scope.row.description + " " + scope.row.type }}
                  </template>
                </el-table-column>
              </el-table>
            </el-tab-pane>
            <el-tab-pane label="Body" name="third" :disabled="textbody == ''">
              <el-input
                v-model="textbody"
                :autosize="{ minRows: 5 }"
                type="textarea"
                placeholder="Please input"
              />
            </el-tab-pane>
          </el-tabs>
        </div>
      </el-collapse-item>
      <el-collapse-item name="2">
        <template #title>
          <el-icon><DocumentRemove /></el-icon>
          {{ $t("postman.response") }}
          <el-tag
            class="mx-1"
            :type="status == '200' ? 'success' : 'danger'"
            size="small"
            effect="plain"
            v-if="status != ''"
            style="margin-left: 3px"
          >
            status:{{ status }}
          </el-tag>
        </template>
        <div class="el-textarea-container">
          <el-input
            v-model="textresult"
            :autosize="{ minRows: 5 }"
            type="textarea"
            placeholder="Please input"
          />
        </div>
      </el-collapse-item>
    </el-collapse>
  </el-scrollbar>
  <el-footer>
    <el-button type="primary" @click="send">
      {{ $t("postman.send") }}
    </el-button>
  </el-footer>
</template>
<style scoped>
.el-textarea-container {
  white-space: pre-line;
}
</style>
<script setup>
import { ref, defineProps, onBeforeUpdate, onMounted, defineExpose } from "vue";
import axios from "axios";
import json5 from "json5";
import { useI18n } from "vue-i18n";

const { t } = useI18n();

const activeNames = ref(["1"]);

const textbody = ref("");
const textresult = ref("");
const activeName = ref("first");
const multipleSelectionHeader = ref([]);
const multipleSelectionParams = ref([]);

const Header = ref([]);
const Params = ref([]);
const props = defineProps(["Data"]);
const status = ref("");

const paramsRef = ref();
const headerRef = ref();

onMounted(init);
onBeforeUpdate(init);
function init() {
  if (props.Data.header != undefined && props.Data.header.length > 0) {
    Header.value = props.Data.header;
    headerRef.value.toggleAllSelection();
  }
  if (props.Data.params != undefined && props.Data.params.length > 0) {
    Params.value = props.Data.params;
    paramsRef.value.toggleAllSelection();
  }
  if (props.Data.body != undefined && props.Data.body.length > 0) {
    let bodyPs = props.Data.body[0];
    let jsonS = toJsonModel(bodyPs);
    textbody.value = JSON.stringify(jsonS, null, 4);
  }
}

function toJsonModel(item) {
  let jsonS = {};
  if (
    item.children != undefined &&
    item.children != null &&
    item.children.length > 0
  ) {
    for (let j = 0; j < item.children.length; j++) {
      jsonS[item.children[j].name] = toJsonModel(item.children[j]);
    }

    //对象或数组对象
    if (0 == item.type.indexOf("array(")) {
      //数组对象值
      return [jsonS];
    } else {
      //对象值
      return jsonS;
    }
  } else {
    //一般类型值
    if (0 == item.type.indexOf("array(")) {
      return [item.type.replace("array(", "").replace(")", "")];
    } else {
      return item.type;
    }
  }
}

function send() {
  status.value = "";
  textresult.value = t("postman.waiting");
  let config = {
    method: props.Data.type,
    url: getHost(props.Data.apiUrl) + props.Data.url,
  };
  try {
    activeNames.value = ["2"];

    if (multipleSelectionHeader.value.length > 0) {
      config.headers = {};
      multipleSelectionHeader.value.forEach((item) => {
        config.headers[item.name] = item.value;
      });
    }

    if (multipleSelectionParams.value.length > 0) {
      config.params = {};
      multipleSelectionParams.value.forEach((item) => {
        config.params[item.name] = item.value;
      });
    }

    if (textbody.value != "") {
      config.data = json5.parse(textbody.value);
    }
  } catch (err) {
    status.value = "";
    textresult.value = err + "  (please check your params!)";
    return;
  }

  axios
    .post("/service/proxy", { data: config })
    .then((response) => {
      if (response.status == 200) status.value = "200";
      textresult.value = JSON.stringify(response.data, null, 4); // 处理服务器响应的数据
    })
    .catch((error) => {
      console.log(error); // 处理请求错误
      status.value = error.response.status;
      textresult.value = JSON.stringify(error.response.data, null, 4);
    });
}

function getHost(url) {
  let prefix = "";
  if (url.indexOf("http://") == 0) {
    prefix = "http://";
  } else if (url.indexOf("https://") == 0) {
    prefix = "https://";
  }
  url = url.replace(prefix, "");
  url = url.substring(0, url.indexOf("/"));
  return prefix + url;
}

const handleSelectionChangeHeader = (val) => {
  multipleSelectionHeader.value = val;
};
const handleSelectionChangeParams = (val) => {
  multipleSelectionParams.value = val;
};
function handleChange() {}

defineExpose({ status });
</script>
